<template>
    <div class="decode container">
        <div id="left-side">
            <div class="back-button" >
                <button @click="back">Back</button>                
            </div>
            <div id="informations" class="item">
                <span class="item-title">Informations</span>
                <ul>
                    <li><strong>IC :</strong> {{ ic }} ({{ detectedLanguage }})
                        <a href="https://www.dcode.fr/index-coincidence" class="tooltip preline" data-balloon=" Index of Coincidence is a cryptanalysis technique studying the probability of finding repeating letters in an encrypted text.
                        English : 0.0667
                        French : 0.0778" data-balloon-length="xlarge" data-balloon-pos="right">?</a>
                    </li>
                    <li><strong>Selected language :</strong> French</li>
                    <li><strong>Analysis time :</strong> 0.095 ms</li>
                </ul>
                <a href="https://en.wikipedia.org/wiki/Polyalphabetic_cipher" v-if="ic < 0.05 && text.length > 10">
                    <div class="warning" data-balloon="A cipher is detected as polyalphabetic when IC is lower than 0.05.
                    For information, the IC of a random text is 0.0385." data-balloon-length="large" data-balloon-pos="right">
                        Warning! Polyalphabetic detected
                    </div>
                </a>
            </div>
            <div id="missing-letters" class="item">
                <span class="item-title">Missing letters in alphabet</span>
                <div class="item-content" id="missing-letters-letters">
                    <div v-for="l in missingLetters" :key="l" :class="{'letter': true, 'letter-active': l == currentLetter}">{{ l }}</div>
                    <div v-if="!missingLetters.length" class="noletter">
                        No missing letters
                    </div>
                </div>
            </div>
            <div id="multiple-letters" class="item">
                <span class="item-title">Multiple letters in alphabet</span>
                <div class="item-content" id="multiple-letters-letters">
                    <Texte :text="multipleLetters" :sub="sub" @change="changeSub" @select="setCurrentLetter" :current-letter="currentLetter"/>
                    <div v-if="!multipleLetters.length" class="noletter">
                        No multiple letters
                    </div>
                </div>
            </div>
            <div id="biggest-words" class="item">
                <span class="item-title">Biggest words</span>
                <div class="item-content" id="biggest-words-letters">
                    <div v-for="(w, i) in biggestWords" :key="i">
                        <Texte :text="w" :sub="sub" @change="changeSub" @select="setCurrentLetter" :current-letter="currentLetter"/>
                    </div>
                    <div v-if="!missingLetters.length" class="noletter">
                        [TODO] Ecrire un message ici...
                    </div>
                </div>
            </div>
            <div class="item" id="debug">
                <span class="item-title">Debug</span>
                <div class="item-content">
                    <p>Letter: {{ currentLetter }}</p>
                </div>
            </div>
        </div>
        <div id="right-side">
            <div id="alphabet" class="item">
                <span class="item-title">Alphabet</span>
                <div class="item-content" id="alphabet-letters">
                    <Alphabet :sub="sub" :locked="locked" @change="changeSub" @lock="toggleLockSub" @select="setCurrentLetter" :current-letter="currentLetter"/>
                    <div class="buttons">
                        <button @click="actionAuto">Auto</button>
                        <button @click="actionRandom">Random</button>
                    </div>
                </div>
                <div class="stickbar">
                    <span class="stickbar-item" data-balloon="Copy the decrypted alphabet"  data-balloon-pos="left"
                        v-clipboard:copy="alphabetText"
                        v-clipboard:success="copySucced"
                        v-clipboard:error="copyError">Copy</span>
                </div>
            </div>
            <div id="cipher" class="item">
                <span class="item-title">Cipher</span>
                <div class="item-content" id="cipher-letters">
                    <TextePage :text="text" :sub="sub" @change="changeSub" @select="setCurrentLetter" :current-letter="currentLetter"/>
                </div>
                <div class="stickbar">
                    <span class="stickbar-item" data-balloon="Copy the decrypted text" data-balloon-length="medium" data-balloon-pos="left"
                        v-clipboard:copy="plainText"
                        v-clipboard:success="copySucced"
                        v-clipboard:error="copyError">Copy</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Texte from './Texte.vue'
import TextePage from './TextePage.vue'
import Alphabet from './Alphabet.vue'
import Vue from 'vue'

export default {
    name: 'Decode',
    props: {
        textIn: String,
        subIn: Object
    },
    data() {
        const locked = {}
        for(let k in this.subIn) {
            locked[k] = false
        }
        return {
            sub: this.subIn,
            locked,
            currentLetter: "",
            // text: "FAR ILJJAR DTKRTKSR, TURRG SQTDUGSA FAR DTKRAUQR (SIA TDVAKSUQA LN SIA DTKEGKC JAK AK VAQRGLK LQGCGKTFA), ARS F'UKA DAR EGKPUTKSA-RGX KLUVAFFAR D'TQSIUQ ELKTK DLYFA JASSTKS AK REAKA FA DASAESGVA RIAQFLEB ILFJAR.F'GKSQGCUA DA FT KLUVAFFA. FAR ILJJAR DTKRTKSR QAOLRA RUQ UKA AKGCJA EQYOSLCQTOIGPUA. DTKR EASSA KLUVAFFA, RIAQFLEB ILFJAR QAURRGS T HQGRAQ FA ELDA DAR JARRTCAR EIGNNQAR PUG SAQQGNGAKS FT NAJJA DA RLK EFGAKS. EAR JARRTCAR RLKS ELJOLRAR DA RUGSAR DA RYJHLFAR DGNNAQAKSR, AK NLQJA DA OAQRLKKTCAR (TOOAFAR RSGEBJAK) TCGSTKS FAR HQTR AS FAR MTJHAR, OTQNLGR JUKGR DA OASGSR DQTOATUX : FAR Â« ILJJAR DTKRTKSR Â».RIAQFLEB ILFJAR OTQVGAKS T ELJOQAKDQA FT RGCKGNGETSGLK DA EAR RAQGAR DA DARRGKR AK ASUDGTKS FAR NQAPUAKEAR D'TOOTQGSGLK DA  EITPUA OAQRLKKTCA, RAFLK FT JASILDA DA F'TKTFYRA NQAPUAKSGAFFA. FA EIGNNQA DA EA EQYOSLCQTJJA ARS AK NTGS SQAR RGJOFA. GF R'TCGS D'UKA RUHRSGSUSGLK TFOITHASGPUA : EITPUA OASGS OAQRLKKTCA QAOQARAKSA UKA FASSQA. F'GKSAQAS DA EA EIGNNQA ARS RT DGREQASGLK : USGFGRA DTKR DAR JARRTCAR CQGHLUGFFAR RUQ DAR JUQR LU DAR HLUSR DA OTOGAQ, GF OTRRA GKTOAQEU ETQ LK OAUS FA OQAKDQA OLUQ UK DARRGK D'AKNTKS -- EA PUA NTGS D'THLQD FA DLESAUQ WTSRLK, HGAK RUQ ! EAR CQGHLUGFFGR RLKS AK NTGS UKA NLQJA DA RSACTKLCQTOIGA, F'TQS DA QAKDQA TKLDGKR FAR JARRTCAR FAR OFUR RAEQASR.FA NFTC DA EAS AOQAUVA ARS AKRGHR{DTKEGKC_JAK_EGOIAQ_GR_JLKLTFOITHASGE}".toLowerCase(),
            // text: "".toLowerCase()
            // text: "".toLowerCase(),
            // text: "".toLowerCase()
            text: this.textIn
        }
    },
    computed: {
        ic() {
            const text = this.text.toLowerCase()
            const letters = {}
            let lettersCount = 0
            for(const l of text) {
                if(!letters[l]) {
                    letters[l] = 1
                } else {
                    letters[l] += 1
                }
                lettersCount += 1
            }
            
            const denom = lettersCount * (lettersCount - 1)

            let ret = 0.0
            for(const v of Object.values(letters)) {
                ret += (v * (v - 1)) / denom
            }
            return Math.round(ret * 1000) / 1000
        },
        detectedLanguage() {
            const ics = {
                "English": 0.0667,
                "French": 0.0778,
                // "Allemand": 0,0762,
                // "Espagnol": 0,0770,
            }
            let best = "English"
            let score = Math.abs(ics[best] - this.ic)
            for(let key in ics) {
                let currentScore = Math.abs(ics[key] - this.ic)
                if(currentScore < score) {
                    best = key
                    score = currentScore
                }
            }
            return best
        },
        multipleLetters() {
            const ret = {}
            for(const cipher in this.sub) {
                const plain = this.sub[cipher]
                for(const cipher2 in this.sub) {
                    if(cipher != cipher2 && plain == this.sub[cipher2]) {
                        Vue.set(ret, cipher2, plain)
                    }
                }
            }

            //Sort by value and return keys
            const sortable = [];
            for (const k in ret) {
                sortable.push([k, ret[k]])
            }

            return sortable.sort((a, b) => a[1] > b[1])
            .map(x => x[0])
            .join('')
        },
        missingLetters() {
            const ret = []
            const cipher = Object.keys(this.sub).join('')
            const plain = Object.values(this.sub).join('')
            for(const l1 of cipher) {
                if(!plain.includes(l1)) {
                    ret.push(l1)
                }
            }
            return ret
        },
        biggestWords(){
            let words = this.text.split(/[ ',/]/)
            words.sort(function(a, b){
                return b.length - a.length;
            });
            return words.slice(0, 5)
            // return ['abeille','voiture','grandmot','cestok']
        },
        plainText() {
            return this.text.split('').map(x => this.sub[x] || x).join('')
        },
        alphabetText() {
            return Object.values(this.sub).join('')
        }
    },
    methods: {
        changeSub({from, to}) {
            console.log('Change sub');
            
            Vue.set(this.sub, from, to)
        },
        toggleLockSub({from}) {
            console.log('Lock sub')
            Vue.set(this.locked, from, !this.locked[from])
        },
        setCurrentLetter(letter) {
            this.currentLetter = letter
        },
        copySucced() {
            // Toast
            this.$toasted.show("Copied in clipboard", { 
                theme: "primary", 
                position: "bottom-right", 
                duration : 2000
            });
        },
        copyError() {
            // Toast
            this.$toasted.show("Failed to copy in clipboard", { 
                theme: "primary",
                type: "error",
                position: "bottom-right", 
                duration : 2000
            });
        },
        actionAuto() {
            console.log('Sub auto');
        },
        actionRandom() {
            console.log('Sub random');
        },
        back() {
            console.log('back')
            this.$emit('return')
        }
    },
    components: {
        Texte,
        Alphabet,
        TextePage
    }
}
</script>

<style scoped>
#missing-letters-letters{
    background-color: #ffd1d1;
}
#multiple-letters-letters{
    background-color: #d1e7ff;
}
#biggest-words-letters{
    background-color: #FCF7E9;
}

#alphabet-letters{
    display: flex;
    background-color: #FCF7E9;
}
#cipher-letters{
    background-color: #F9EFD3;
}

.letter{
    vertical-align: top;
    display:inline-block;
    margin: -1px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    width: 12px;
    height: 20px;
    padding: 0px 3px 0px 3px;
    background: linear-gradient(#fcf8ea,#ffffff,#f8efd3);
    font: normal 12px/22px "Lucida Console", "Trebuchet MS", Arial, Helvetica, sans-serif;
    text-align: center;
    /* text-transform: uppercase; */
    color: #63562E;
    border: #D0BE90 solid 1px;
}
.letter:hover{
    font-weight: 700;
}
.letter-active {
    font-weight: 700;
    color: #d00;
}
.back-button{
    margin: 5px;
    /* box-sizing: border-box; */
    /* width: 100%; */
}
.back-button button{
    width: 100%;
    padding: 5px 0;
    background-color: #bab185;
    border: 0;
    color: #fff;
    cursor: pointer;
}
.back-button button:before{
    content: 'â¬… ';
    position: relative;
    top: 1px;
    font-size: 16px;
}
.back-button button:hover{
    background-color: #cbc194;
}
.warning{
    margin-top: 5px;
    padding: 5px 0;
    font-size: 14px;
    text-align: center;
    /* background-color: #ff9d5e; */
    border: 1px solid #ffddc7;
    color: #ff9d5e;
}

.noletter{
    font-size: 14px;
    text-align: center;
}
#missing-letters-letters .noletter{
    color: #f2a2a2;
}
#multiple-letters-letters .noletter{
    color: #a9c0da;
}

#alphabet-letters .text{
    width: 100%;
}
#alphabet-letters .buttons button{
    margin: 1px 0;
    width: 100%;
    padding: 4px 0;
    background-color: #f3ebd5;
    border: 0;
    color: #fff;
    cursor: pointer;
}
#alphabet-letters .buttons button:hover{
    background-color: #ece1c5;
}
</style>



